.fadeTranslate-enter {
    opacity: 0;
    -webkit-transform: translate(0, -3vh);
        -ms-transform: translate(0, -3vh);
            transform: translate(0, -3vh);
    position: fixed;
  }
  
  .fadeTranslate-enter.fadeTranslate-enter-active {
    opacity: 1;
    -webkit-transform: translate(0, 0);
        -ms-transform: translate(0, 0);
            transform: translate(0, 0);
    -webkit-transition: opacity 500ms ease-in 500ms, -webkit-transform 500ms ease-in-out 500ms;
    transition: opacity 500ms ease-in 500ms, -webkit-transform 500ms ease-in-out 500ms;
    -o-transition: opacity 500ms ease-in 500ms, transform 500ms ease-in-out 500ms;
    transition: opacity 500ms ease-in 500ms, transform 500ms ease-in-out 500ms;
    transition: opacity 500ms ease-in 500ms, transform 500ms ease-in-out 500ms, -webkit-transform 500ms ease-in-out 500ms;
  }
  
  .fadeTranslate-exit {
    opacity: 1;
    position: fixed;
    -webkit-transform: translate(0, 0);
        -ms-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  
  .fadeTranslate-exit.fadeTranslate-exit-active {
    opacity: 0;
    -webkit-transform: translate(0, 3vh);
        -ms-transform: translate(0, 3vh);
            transform: translate(0, 3vh);
    -webkit-transition: opacity 300ms ease-in, -webkit-transform 300ms ease-in-out;
    transition: opacity 300ms ease-in, -webkit-transform 300ms ease-in-out;
    -o-transition: opacity 300ms ease-in, transform 300ms ease-in-out;
    transition: opacity 300ms ease-in, transform 300ms ease-in-out;
    transition: opacity 300ms ease-in, transform 300ms ease-in-out, -webkit-transform 300ms ease-in-out;
  }
  
  .fix-container {
    position: fixed;
  }


